img {
  max-width: 100%; }

a {
  text-decoration: none;
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  scroll-behavior: smooth; }

.page-body {
  font-family: "Montserrat", "Arial", sans-serif;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #011c40;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  position: relative; }

.wrapper {
  margin-left: 103px;
  margin-right: 103px; }
  @media (max-width: 1365px) {
    .wrapper {
      margin-left: 45px;
      margin-right: 45px; } }
  @media (max-width: 767px) {
    .wrapper {
      margin-left: 15px;
      margin-right: 15px; } }

.page-header {
  min-height: 650px;
  background-color: #6ba8d6;
  background-image: url("../img/content/head-pictures-desktop.jpg");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  color: #F9FBFD; }
  @media (max-width: 1366px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/content/head-pictures-desktop@2x.jpg"); } }
  @media (max-width: 1365px) {
    .page-header {
      min-height: 638px;
      background-image: url("../img/content/head-pictures-tablet.jpg"); } }
  @media (max-width: 1365px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/content/head-pictures-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .page-header {
      position: relative;
      min-height: 500px;
      background-position: bottom;
      background-image: url("../img/content/head-pictures-mobile.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../img/content/head-pictures-mobile@2x.jpg"); } }

@media (max-width: 1365px) {
  .page-header__logo-desktop {
    display: none; } }

.page-header__logo-tablet {
  display: none; }
  @media (max-width: 1365px) {
    .page-header__logo-tablet {
      display: block; } }
  @media (max-width: 767px) {
    .page-header__logo-tablet {
      display: none; } }

.page-header__logo-mobile {
  display: none; }

.page-header__logo-mobile-menu {
  display: none; }
  @media (max-width: 767px) {
    .page-header__logo-mobile-menu {
      display: block;
      position: absolute;
      top: 20px;
      left: 50px; } }

.page-header__toggle {
  display: none; }

.page-header__main-nav {
  display: flex;
  margin-top: 25px;
  margin-bottom: 25px;
  justify-content: space-between; }
  @media (max-width: 1365px) {
    .page-header__main-nav {
      margin-top: 15px;
      margin-bottom: 15px; } }
  @media (max-width: 767px) {
    .page-header__main-nav {
      width: 100%;
      background-color: #F9FBFD;
      color: #011C40;
      margin-top: 0;
      margin-bottom: 0;
      flex-direction: column;
      justify-content: flex-start; } }

.page-header__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px; }
  @media (max-width: 1365px) {
