      line-height: 22px;
      padding-left: 36px;
      margin-top: 55px;
      margin-bottom: 313px; } }

.page-header__list li {
  margin-top: 4px; }
  @media (max-width: 767px) {
    .page-header__list li {
      margin-top: 30px;
      margin-bottom: -2px;
      padding-left: 24px; } }

.page-header__list li:not(:last-child) {
  margin-right: 34px; }
  @media (max-width: 1365px) {
    .page-header__list li:not(:last-child) {
      margin-right: 24px; } }

.page-header__list a {
  color: #F9FBFD; }
  @media (max-width: 767px) {
    .page-header__list a {
      color: #011C40; } }

.page-header__logo-wrapper {
  margin-top: 10px; }
  @media (max-width: 1365px) {
