@media (max-width: 767px) {
  .page-header--closed .page-header__list {
    display: none; } }

@media (max-width: 767px) {
  .page-header--open .page-header__toggle,
  .page-header--closed .page-header__toggle {
    display: block; } }

.page-header--closed .page-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #F9FBFD;
  box-shadow: 0 6px 0 0 #F9FBFD, 0 12px 0 0 #F9FBFD; }

@media (max-width: 767px) {
  .page-header--closed .page-header__logo-mobile-menu {
    display: none; } }

@media (max-width: 767px) {
  .page-header--closed .page-header__logo-mobile {
    display: block;
    position: absolute;
    top: 20px;
    left: 15px; } }

@media (max-width: 767px) {
  .page-header--open .page-header__logo-mobile-menu {
    display: block;
    position: absolute;
    top: 20px;
    left: 50px; } }

@media (max-width: 767px) {
  .page-header--open .page-header__nav-wrapper {
    background-color: #002b6680;
    position: absolute;
    min-height: 568px;
    width: 100%;
    z-index: 3;
    top: 0;
    right: 0; } }

@media (max-width: 767px) {
  .page-header--open .page-header__main-nav {
    margin-left: 35px; } }

@media (max-width: 767px) {
  .page-header--open .page-header__wrapper {
    display: none; } }

.page-header--open .page-header__toggle {
  width: 15px;
  height: 15px;
  background-color: inherit;
  top: 22px;
  right: 15px; }
  .page-header--open .page-header__toggle::before, .page-header--open .page-header__toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 18px;
    height: 2px;
    background-color: #011C40; }
  .page-header--open .page-header__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .page-header--open .page-header__toggle::after {
    transform: rotate(-45deg); }

.page-header--open .page-header__list {
  padding-right: 46px;
  padding-left: 0; }

.page-header--open .page-header__logo-wrapper {
  left: 50px; }

@media (max-width: 767px) {
  .main-nav__toggle {
    position: absolute;
    right: 15px;
    top: 24px;
    z-index: 2;
    width: 24px;
    height: 12px;
    cursor: pointer;
    border: none;
    background-color: inherit; } }
